import React from 'react';
import { useTranslation, Trans } from 'react-i18next';
import { useHistory, useLocation } from 'react-router-dom';
import usePortal from 'react-useportal';

// Components
import WizardButtons from 'components/WizardButtons';

// Images
import { ArrowRightSVG } from 'containers/Welcome/style';

// Helpers
import { isLanguage } from 'helper/isLanguageHelper';

// Header Control
import useHeaderContext from 'hooks/useHeaderContext';

// Utils
import { scrollToTop } from 'helper/scrollHelper';

// Styles
import { colors } from '../../../../theme';
import {
  PredictionResultContainer,
  TitleResult,
  IntroText,
  StyledLow,
  StyledHigh,
  StyledLowUr,
  StyledHighUr,
} from './style';

interface PredictionResultLocation {
  predictionResult?: string;
  submissionId?: string;
}

const PredictionResult = ({
  nextStep,
}: Wizard.StepProps) => {
  // Hooks
  const {
    setDoGoBack, setTitle, setSubtitle, setType,
  } = useHeaderContext();
  const { t, i18n } = useTranslation();
  const history = useHistory();
  const { Portal } = usePortal({
    bindTo: document && document.getElementById('wizard-buttons') as HTMLDivElement,
  });
  const location = useLocation<PredictionResultLocation>();

  const predictionResult = location.state?.predictionResult;

  const handleReturnMain = React.useCallback(() => {
    history.replace('step-record/cough');
  }, [history]);

  const handleOnSubmit = () => {
    if (nextStep) {
      history.push(nextStep, {
        submissionId: location.state.submissionId,
      });
    }
  };

  const urduLang = React.useMemo(() => isLanguage(i18n.language, ['ur']), [i18n.language]);
  const hasResults: boolean = predictionResult === 'positive' || predictionResult === 'negative';
  const validateLocale: string | JSX.Element = (urduLang ? <ArrowRightSVG /> : t('nextButton'));

  // Effects
  React.useEffect(() => {
    scrollToTop();
    setTitle('');
    setDoGoBack(() => {});
    setType('tertiary');
    setSubtitle(`${t('predictionResult:result')}`);
    setType('tertiary');
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const renderResult = React.useMemo(() => {
    if (predictionResult === 'positive') {
      return (
        <>
          <TitleResult color={colors.red}>
            {t('predictionResult:resultDetected')}
          </TitleResult>
          {urduLang ? <StyledHighUr /> : <StyledHigh />}
          <IntroText>
            <Trans i18nKey="predictionResult:resultDetectedText">
              {/* eslint-disable-next-line max-len */}
              <strong>Please contact your healthcare professional</strong> and take additional precautions.<br /><br />Your cough has similar patterns to our database of COVID-19 coughs.
            </Trans>
          </IntroText>
        </>
      );
    }
    if (predictionResult === 'negative') {
      return (
        <>
          <TitleResult color={colors.green}>
            {t('predictionResult:resultNotDetected')}
          </TitleResult>
          {urduLang ? <StyledLowUr /> : <StyledLow />}
          <IntroText>
            <Trans i18nKey="predictionResult:resultNotDetectedText">
              {/* eslint-disable-next-line max-len */}
              Please <strong>continue to take appropriate measures</strong> based on the advice of your healthcare professional or applicable regulatory body and reassess yourself in our app daily. <br /><br />Your cough does not have similar patterns to our database of COVID-19 coughs.
            </Trans>
          </IntroText>
        </>
      );
    }
    return (
      <>
        <TitleResult color={colors.black}>
          {t('predictionResult:resultNotAnalyze')}
        </TitleResult>
        <IntroText>
          <Trans i18nKey="predictionResult:resultNotAnalyzeText">
            {/* eslint-disable-next-line max-len */}
            Our algorithm is not able to analyze your cough. Please <strong>submit another recording of your cough</strong>.
          </Trans>
        </IntroText>
      </>
    );
  }, [predictionResult, t, urduLang]);

  return (
    <>
      <PredictionResultContainer>
        <TitleResult>
          {t('predictionResult:results')}
        </TitleResult>
        {renderResult}
      </PredictionResultContainer>

      {/* Bottom Buttons */}
      <Portal>
        <IntroText>
          <Trans i18nKey="predictionResult:noteStatement">
            {/* eslint-disable-next-line max-len */}
            <strong>Important note:</strong> This form is for informational purposes and data collection only. It will not diagnose your  respiratory status or diagnose any disease, disorder, or other health condition. Virufy is conducting research and will use the information you provide for research. The Virufy app doesnt replace a doctor. Remember that it is your responsibility to seek medical advice from your doctor.
          </Trans>
        </IntroText>
        <WizardButtons
          invert
          leftSide={hasResults ? validateLocale : t('predictionResult:nextButton')}
          leftHandler={hasResults ? handleOnSubmit : handleReturnMain}
        />

      </Portal>
    </>
  );
};

export default React.memo(PredictionResult);
